<script>
import { Toast } from "mint-ui";
// import CardToken from "../../build/CardTokenEx.json";
// import { CardTokens } from "../common/config";
import heroList from "../common/heroIfon.json";
import NewPopup from "../model/NewPopup";

export default {
  name: "backpack",
  data() {
    return {
    
      notbuy:true,
      notcard:false,
      notorder:true,
      NewPopup: false,
      showModle: 1,
      showText: "",
      Oisf: false,
      pickList: [],
      phat: "../assets/cardimg/",
      cardList: [],
      heroList: heroList.heroList,
      NavLsit: [
        {
          navName: "",
          sate: true,
          index: 1000,
          style: "backpack-nav-list5",
        },
        {
          navName: "白",
          sate: false,
          index: 5,
          style: "backpack-nav-list",
        },
        {
          navName: "蓝",
          sate: false,
          index: 4,
          style: "backpack-nav-list1",
        },
        {
          navName: "紫",
          sate: false,
          index: 3,
          style: "backpack-nav-list2",
        },
        {
          navName: "橙",
          sate: false,
          index: 2,
          style: "backpack-nav-list3",
        },
        {
          navName: "金",
          sate: false,
          index: 1,
          style: "backpack-nav-list4",
        },
      ],
      account: "",
      provider: "",
      inex: 0,
      mixNum: 3,
      Web3: "",
    };
  },
  components: {
    NewPopup,
    // homeListview
  },
  async mounted() {
    await this.$getWbe3.Init(
      (addr) => {
        //得到相应的钱包地址
        this.account = addr;
        // console.log(this.account);
        var r = 1000;
        this.getCardToken(r);
      },
      (provider) => {
        this.Web3 = provider;
        // console.log(this.Web3);
      }
    );
    await this.$store.mutations.initWeb3Account();
    this.cardList = [];

    if (this.showModle == 1) {
      this.showText = "选择合成";
    } else {
      this.showText = "去合成";
    }
  },
  methods: {
            depositBtn() {
      this.$router.push("/backpack");
    },
    exchangeBtn() {
      this.$router.push("/exchange");
    },
    combinationBtn() {
      this.$router.push("/combinationEit");
    },
    buybotton(){
      this.notbuy=false
      this.notcard=true
      this.notorder=true
    },
    cardbotton(){
      this.notbuy=true
      this.notcard=false
      this.notorder=true
    },
    orderbotton(){
       this.notbuy=true
      this.notcard=true
      this.notorder=false
    },
    headerBtn() {
      this.NewPopup = true;
    },
    popUpBtnMethod() {
      this.NewPopup = false;
    },
    async NavBtn(e) {
      for (let i in this.NavLsit) {
        this.NavLsit[i].sate = false;
      }
      e.sate = true;
      var r = e.index;
      await this.getCardToken(r);
    },
    async getCardToken(r) {
      this.cardList = [];
      // console.log(this.account);
      let contract = await this.$Fun.CardDetail();
      await this.$getWbe3.getTokenIds(contract, this.account, r, (res) => {
        // console.log(res, 999);
        if (res) {
          for (var i in res) {
            if (res[i].length > 0) {
              var obj = {};

              this.heroList.map((item) => {
                if (res[i][2] == item.no) {
                  obj.index = i;
                  obj.sate = false;
                  obj.tokenId = res[i][0];
                  obj.power = res[i][1];
                  obj.genre = res[i][3];
                  obj.genre = res[i][3];
                  obj.imgUrlC = res[i][2];
                  obj.imgUrl = require("../assets/cardimg/" +
                    res[i][2] +
                    ".png");
                  obj.doc = item.doc;
                  obj.name = item.name;
                  obj.name1 = item.name1;
                  obj.times = res[i][4];
                  obj.Alltimes = res[i][6];
                }
              });
              this.cardList.push(obj);
            }
          }
        }
      });
    },
    backBtn() {
      this.$router.push("/home");
    },
    cardBtnInfo() {
      if (this.showModle == 1) {
        this.showModle = 2;
      } else {
        this.showModle = 1;
        if (this.pickList.length > 0 && this.pickList.length == this.mixNum) {
          this.$router.push("/fusing");
        } else {
          this.showModle = 2;
          Toast("五张蓝卡合一张紫卡,三张紫卡合一张橙卡，橙卡金卡不可合成");
          Toast({
            message: "五张蓝卡合一张紫卡,三张紫卡合一张橙卡，橙卡金卡不可合成",
            position: "middle",
            duration: 1000,
          });
          Toast.message = " ";
      
        }
      }
    },
    cardBtn(e) {
      if (this.showModle == 2) {
        e.sate = !e.sate;

        if (e.genre == "1" || e.genre == "2" || e.genre == "0") {
          // Toast("该卡片不可合成");
          Toast({
            message: "该卡片不可合成",
            position: "middle",
            duration: 1000,
          });
          Toast.message = " ";
          e.sate = false;
          return true;
        }

        if (this.pickList.length > 0) {
          if (this.pickList[0].genre == e.genre) {
            if (e.genre == "5") {
              this.mixNum = 10;
            }
            if (e.genre == "4") {
              this.mixNum = 5;
            }
            if (e.genre == "3") {
              this.mixNum = 3;
            }
            for (var i in this.pickList) {
              if (this.pickList[i].sate == false) {
                this.pickList.splice(i, 1);
              }
            }
          } else {
            e.sate = false;
            // Toast("请选择相同品质的卡牌进行合成");
            Toast({
              message: "请选择相同品质的卡牌进行合成",
              position: "middle",
              duration: 1000,
            });
            Toast.message = " ";
          }
        }

        if (this.pickList.length >= this.mixNum) {
          e.sate = false;

          // Toast("该卡片最多可选择" + this.mixNum + "张");
          Toast({
            message: "该卡片最多可选择" + this.mixNum + "张",
            position: "middle",
            duration: 1000,
          });
          Toast.message = " ";
          return false;
        } else {
          if (e.sate == true) {
            this.pickList.push(e);
          }
        }

        if (this.pickList.length > 0 && this.pickList.length < this.mixNum) {
          this.Oisf = false;
        } else {
          this.Oisf = true;
        }
        sessionStorage.setItem("cardList", JSON.stringify(this.pickList)); //存储钱包地址
      } else {
        this.$router.push("/cardInfo");
        sessionStorage.setItem("cardInfo", JSON.stringify(e)); //存储钱包地址
      }
    },
  },
};
</script>

